import React, { useRef, useState } from 'react';
import { Canvas, useFrame } from 'react-three-fiber';
import { Text } from '@react-three/drei'

const textProps = {
  fontSize: 0.05,
  font: 'Lato',
  whiteSpace: "normal",
  maxWidth: ".9"
}
const q2Text = "Q2 2021\n• Design and refine UI of the complete project.\n• Creation of the CardDex whitepaper.\n• Social Media presence to engage with the CardDex community, and keep up to date on our progress.\n\t\t\u203A Twitter - Stay up to date with @CardDex updates on our Twitter.\n\t\t\u203A Telegrams - Meet the dev, and engage with fellow CardDex DeFi mavericks on @CardDex.\n\t\t\u203A Reddit - Engage with the CardDex community on our Subreddit page."
const q3Text = "Q3 2021\n• Create a functional demo & Confirm CardDex Functionality on Alonzo Testnet.\n• Launch/ICO (Initial Card Offering). "
const q4Text = "Q4 2021\n• Launch on Cardano Guguen Mainnet with the Plutus Smart Contracts Deployment.\n• Solidify niche in ADA ecosystem.\n• Expand technical capabilities."
const scaling = 1000

function Box(props) {
  const mesh = useRef()
  const [state, setState] = useState({isHovered: false, isActive: false})
  useFrame(state => {
    const time = state.clock.getElapsedTime();
    //mesh.current.position.y = mesh.current.position.y + Math.sin(time*2)/100;
    mesh.current.position.y = -window.pageYOffset/1000 + 2.4
    mesh.current.position.y = mesh.current.position.y + Math.sin(time*2)/20;
    mesh.current.rotation.x = -window.pageYOffset/160 + .5;
    console.log(-window.pageYOffset/1000 + 2.4);
    if(window.innerWidth > 1000)
    {
      mesh.current.scale.x = 1.9;
      mesh.current.scale.y = 1.9;
      mesh.current.scale.z = 1.9;
    }
    else
    {
      mesh.current.scale.x = 1.5;
      mesh.current.scale.y = 1.5;
      mesh.current.scale.z = 1.5;
    }
  })

  return (
    <mesh
      {...props}
      ref={mesh}
      //scale={state.isHovered ? [2, 2, 2] : [4, 4, 4]}
      //scale={[window.innerWidth/scaleing, window.innerWidth/scaleing, window.innerWidth/scaleing]}
      onClick={(e) => setState({...state, isActive: !state.isActive, })}
      onPointerOver={(e) => setState({...state, isHovered: true})}
      onPointerOut={(e) => setState({...state, isHovered: false})}>
      <boxBufferGeometry args={[1, 1, 1]} />
      <meshStandardMaterial color={state.isActive ? '#820263' : '#D90368'} />
      <Text depthTest={false} material-toneMapped={false} {...textProps}  position={[0, 0, 0.51]} rotation={[0, 0, 0]} fontSize="0.2">
        Timeline
      </Text>
      <Text depthTest={false} material-toneMapped={false} {...textProps}  position={[0, -0.51, 0]} rotation={[1.5708, 0, 0]} text={q2Text} />
      <Text depthTest={false} material-toneMapped={false} {...textProps}  position={[0, 0, -0.51]} rotation={[-3.2, 0, 0]} text={q3Text} />
      <Text depthTest={false} material-toneMapped={false} {...textProps}  position={[0, 0.51, 0]} rotation={[-1.5708, 0, 0]} text={q4Text} />
    </mesh>
  )
}

export default function App() {
  
  return (
    <>
    <br/><br/><br/><br/><br/><br/><br/><br/>
    <Canvas style={{height: `1300px`}}>
      <ambientLight intensity={0.5} />
      <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} />
      <pointLight position={[-10, -10, -10]} />
      {/*<Box position={[-4, 0, 0]} />*/}
      <Box position={[0, 0, 0]} />
    </Canvas>
    </>
  )
}